.app {
    width: 100%;
}

.header {
    width: 100%;
    height: 61px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 24px;
    background-color: var(--hover-blue);
}

.header .text {
    font-family: 'Montserrat', sans-serif;
    font-size: 24px;
    font-weight: bold;
    color: #fff;
}

.header img {
    mix-blend-mode: plus-lighter;
}

.promt-section-wrapper {
    padding: 0 24px 24px 24px;
    background: linear-gradient(0deg, rgba(31, 99, 203, 0.05) 0%, rgba(31, 99, 203, 0.05) 100%), #FFF;
}

.promt-section-inner {
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
    background-color: #fff;
    padding: 0 24px 24px 24px;
}
