.promptTextarea {
  height: 200px;
  border: 1px solid var(--blue);
  border-radius: 4px;
  padding: 8px;
  color: var(--gray);
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
}

.inputTextarea {
  font-family: 'Montserrat', sans-serif;
  border-radius: 4px;
  background: #FAFAFA;
  padding: 8px;
  height: 360px;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: var(--blue);
  border: 1px solid var(--blue);
}

.inputTextarea:focus, 
.inputTextarea:active, 
.inputTextarea:focus-visible,
.promptTextarea:focus, 
.promptTextarea:active, 
.promptTextarea:focus-visible {
  outline: none;
}

.runAllWrap {
  display: flex;
  align-items: flex-end;
}

.runAllBtn {
  height: 37px;
  min-width: 50px;
  margin-left: 10px;
}

.promt-wrapper {
  padding: 0 24px 8px 24px;
}

.experiment-wrapper {
  padding: 24px 24px 0 24px;
  background: linear-gradient(0deg, rgba(31, 99, 203, 0.05) 0%, rgba(31, 99, 203, 0.05) 100%), #FFF;
}

.experiment-wrapper-inner {
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  background-color: #fff;
  padding: 24px 24px 0 24px;
}

.topPromptBar {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
