:root {
  --primary: #212121;
  --secondary: #00a6ff;
  --success: #43b543;
  --info: #000000;
  --warning: #fb8e00;
  --danger: #ff4133;

  --indigo: #0576b3;
  --purple: #0ee0e0;
  --pink: #b974ff;
  --red: #962626;
  --orange: #fb8e00;
  --green: #3f973f;

  --gray-100: #121212;
  --gray-200: #181818;
  --gray-300: #282828;
  --gray-400: #313131;
  --gray-500: #313131;
  --gray-600: #343434;
  --gray-700: #343434;
  --gray-800: #393939;
  --gray-900: #3f3f3f;

  --icon-disabled: #555555;
  --button: #555555;
  --button-hover: #909090;
  --button-primary: #0288d1;
  --button-primary-hover: #00a6ff;
  --button-destructive: #cc2f2f;
  --button-destructive-hover: #962626;
  --hint-text: #757575;
  --icon1: #bdbdbd;
  --icon2: #bdbdbd;
  --text1: #dcdcdc;
  --text2: #ffffff;
  --text3: #bdbdbd;
  --text4: #000000;
  --filter-row-hover: #00a6ff;
  --filter-row-focused: #0288d1;
  --checkbox-checked: #0288d1;
  --input-focused-error: #463331;
  --input-focused: #0288d1;
  --row-expanded: #512da8;
  --cell-selected: #555555;
  --cell-hovered: #404040;

  --header-background: #212121;
  --input-background-normal: #313131;
  --input-outline-normal: #343434;

  --app-background-color: #ffffff;
  --textarea-background-color: #ffffff;
  --block-background-color: #bdbdbd;

  /* new */
  --blue: #101F68;
  --light-blue: rgba(16, 31, 104, 0.40);
  --light-grey: #EAEDEF;
  --orange: #FF6D00;
  --hover-blue: #0E47A0;
  --gray: #727272;
}

html {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--app-background-color);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

.text-white {
  color: var(--text2);
}

.text-dark {
  color: var(--text4);
}


.text-primary {
  color: var(--primary)
}

.bar {
  display: flex;
  justify-content: flex-start;
  gap: 20px;
  flex-wrap: wrap;
}

.dropdown {
  width: 330px;
  background-color: #fff;
}

.dropdown-control {
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  line-height: 20px;
  border-radius: 4px;
  border: 1px solid var(--blue);
  color: var(--blue);
  padding: 8px 52px 8px 8px;
}

.dropdown-placeholder {
  font-family: 'Montserrat', sans-serif;
  color: var(--blue);
}

.dropdown-arrow {
  position: absolute;
  right: 8px;
  top: 9px;
  cursor: pointer;
}

.dropdown-arrow-rotate {
  transform: rotate(180deg);
}

.dropdown-menu {
  border: 1px solid var(--blue);
}

.dropdown-menu .Dropdown-option {
  font-family: 'Montserrat', sans-serif;
  color: var(--blue);
}

.Dropdown-disabled .dropdown-placeholder {
  color: var(--gray);
}

.Dropdown-disabled {
  border: 1px solid var(--gray);
}

.mt-1 {
  margin-top: 10px;
}

.mt-2 {
  margin-top: 20px;
}

.mt-3 {
  margin-top: 30px;
}

.mb-0-5 {
  margin-bottom: 5px;
}

.mb-1 {
  margin-bottom: 10px;
}

.mb-2 {
  margin-bottom: 20px;
}

.mb-3 {
  margin-bottom: 30px;
}

.input-label {
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: var(--blue);
  font-weight: 400;
}

.ai-selector-header  {
  font-family: 'Montserrat', sans-serif;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 0.5px;
  font-weight: 600;
  color: var(--orange);
}
