.button {
    cursor: pointer;
    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    font-weight: 600;
    line-height: 18px;
    border: none;
    border-radius: 4px;
}

.button.primary {
    background-color: var(--orange);
    color: #fff;
    border: 2px solid var(--orange);
    padding: 7px 24px;
}

.button.primary:hover {
    background-color: #fff;
    color: var(--orange);
}

.button.primary:disabled {
    border: 1px solid var(--gray);
    background: var(--gray);
    color: #fff;
}

.button.secondary {
    background-color: #fff;
    color: var(--blue);
    border: 2px solid var(--blue);
    padding: 7px 24px;
}

.button.secondary:hover {
    background-color: var(--blue);
    color: #fff;
}

.button.small {
    background-color: #fff;
    color: var(--blue);
    border: 1px solid var(--blue);
    font-size: 16px;
    line-height: 20px;
    padding: 8px;
}

.button.small:hover {
    background-color: var(--blue);
    color: #fff;
}
