.w-full {
  width: 100%;
}

table {
  border: 1px solid var(--light-blue);
  border-collapse: collapse;
}

th, td {
  border-bottom: 1px solid var(--light-blue);
  border-right: 1px solid var(--light-blue);
  padding: 2px 4px;
}

tbody tr:nth-of-type(2n + 1) {
  background-color: var(--light-grey);
}

tbody tr {
  color: var(--blue);
}

thead tr {
  background-color: var(--blue);
  color: #fff;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
}

thead tr th {
  font-weight: normal;
}

.section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 17px;
}

.total {
  display: flex;
  gap: 28px;
  font-family: 'Montserrat', sans-serif;
  font-size: 18px;
  font-weight: bold;
}

.total-label {
  color: var(--blue);
}

.total-score {
  color: var(--orange);
}

.score-section {
  padding: 0 24px;
  margin-bottom: 24px;
  margin-top: 8px;
}