.resultTextarea {
    font-family: 'Montserrat', sans-serif;
    border-radius: 4px;
    background: #FAFAFA;
    padding: 8px;
    height: 140px;
    font-size: 16px;
    line-height: 20px;
    font-weight: 400;
    color: var(--gray);
    border: 1px solid var(--blue);
}

.result-wrapper {
    padding: 0 24px;
}

.error {
    color: var(--red);
}